<template>
	<div class="list-page" :style='{}'>
        <div class="breadcrumb-wrapper" style="width: 100%;">
            <div class="back_view" v-if="centerType">
                <el-button class="back_btn" @click="backClick" type="primary"></el-button>
            </div>
        </div>
		<div class="section_title">
            <span>{{formName}}</span>
		</div>
		<el-form :model="searchQuery" class="list_search">
			<div class="search_view">
				<div class="search_label">
					
				</div>
				<div class="search_box">
					<el-input class="search_inp" v-model="searchQuery.papername" placeholder=""
						size="small" clearable>
					</el-input>
				</div>
			</div>
			<div class="search_btn_view">
				<el-button class="search_btn" type="primary" @click="searchClick"></el-button>
			</div>
		</el-form>
		<div class="page_list">
			<div class="data_box">
				<div class="table_view">
					<el-table v-loading="listLoading" border :stripe='true' ref="table" :data="list">
						<el-table-column label="" width="120" :resizable='true' align="left" header-align="left">
							<template #default="scope">{{ scope.$index + 1}}</template>
						</el-table-column>
						<el-table-column label="" :resizable='true' align="left" header-align="left">
							<template #default="scope">
								{{scope.row.username}}
							</template>
						</el-table-column>
						<el-table-column label="" :resizable='true' align="left" header-align="left">
							<template #default="scope">
								{{scope.row.papername}}
							</template>
						</el-table-column>
						<el-table-column label="" :resizable='true' align="left" header-align="left">
							<template #default="scope">
								<el-tag type="success" v-if="scope.row.myscore>0 && !scope.row.ismark">{{scope.row.myscore}}</el-tag>
								<el-tag type="danger" v-else-if="scope.row.myscore==0 && !scope.row.ismark">{{scope.row.myscore}}</el-tag>
							</template>

						</el-table-column>
						<el-table-column label="" :resizable='true' align="left" header-align="left">
							<template #default="scope">
								<el-button class="viewExam_btn" :type="'primary'" @click="infoClick(scope.row)" size="small"></el-button>
							</template>
						</el-table-column>
					</el-table>
				</div>
				<el-pagination
					background 
					:layout="layouts.join(',')"
					:total="total" 
					:page-size="listQuery.limit"
                    v-model:current-page="listQuery.page"
					prev-text=""
					next-text=""
					:hide-on-single-page="false"
					:style='{}'
					@size-change="sizeChange"
					@current-change="currentChange"/>
			</div>
		</div>
		<formModel ref="formRef" @formModelChange="formModelChange"></formModel>
	</div>
</template>

<script setup>
	import {
		ref,
		nextTick,
		getCurrentInstance
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	import {
		ElMessageBox
	} from 'element-plus'
	import formModel from './examrecordFormModel.vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	//
	const tableName = 'examrecord'
	const formName = ''
	const router = useRouter()
	const route = useRoute() 
	//
	const listLoading = ref(false)
	const list = ref([])
	//
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const total = ref(0)
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//
	//
	const btnAuth = (e, a) => {
		return context?.$toolUtil.isAuth(e, a)
	}
	//
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	const table = ref(null)
	const listChange = (row) => {
		nextTick(() => {
			table.value.clearSelection()
			table.value.toggleRowSelection(row)
		})
	}
	const listQuery = ref({
		page: 1,
		limit: 20,
	})
	const searchQuery = ref({})
	const selRows = ref([])
	//
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		if (searchQuery.value.papername && searchQuery.value.papername != '') {
			params['papername'] = '%' + searchQuery.value.papername + '%'
		}
		context?.$http({
			url: `examrecord/groupby`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	const formRef = ref(null)
	const formModelChange = () => {
		searchClick()
	}
	const infoClick = (row) => {
		nextTick(() => {
			formRef.value.init(row)
		})
	}
	//
	const centerType = ref(false)
	//
	const backClick = () => {
		router.push(`/index/${context?.$toolUtil.storageGet('frontSessionTable')}Center`)
	}
	const init = () => {
		if (route.query.centerType) {
			centerType.value = true
		}
		getList()
	}
	init()
</script>

<style lang="scss" scoped>
	// 
	.back_view {
		border-radius: 4px;
		padding: 10px 0px;
		margin: 10px auto;
		z-index: 999;
		background: none;
		width: 100%;
		text-align: right;
		// 
		.back_btn {
			border: 1px solid #0076ca50;
			cursor: pointer;
			border-radius: 4px;
			padding: 0 24px;
			color: #0076ca;
			background: #fff;
			width: auto;
			font-size: 14px;
			height: 34px;
		}
		// -
		.back_btn:hover {
		}
	}
	//
	.list_search {
		.search_view {
			.search_label {
			}
			.search_box {
				// 
				:deep(.search_inp) {

				}
			}
		}
		.search_btn_view {
			// 
			.search_btn {
			}
			// -
			.search_btn:hover {
			}
		}
	}
	// 
	.page_list {
		//
		.data_box {
			.data_view {
				// 
				.el-table {
					padding: 0;
					background: #fff;
					width: 100%;
					border-color: #eee;
					border-width: 1px 0 0 1px;
					border-style: solid;
					:deep(.el-table__header-wrapper) {
						thead {
							width: 100%;
							tr {
								background: #fff;
								th {
									padding: 10px 0;
									border-color: #eee;
									border-width: 0 1px 1px 0;
									border-style: solid;
									text-align: left;
									.cell {
										padding: 0 0 0 5px;
										word-wrap: normal;
										color: #666;
										white-space: normal;
										font-weight: bold;
										display: flex;
										vertical-align: middle;
										font-size: 13px;
										line-height: 24px;
										text-overflow: ellipsis;
										word-break: break-all;
										width: 100%;
										align-items: center;
										position: relative;
										//
										.el-checkbox {
											//
											.el-checkbox__inner {
												background: #fff;
												border-color: #999;
											}
										}
										//
										.is-checked {
											//
											.el-checkbox__inner {
												background: #55ffff;
												border-color: #55ffff;
											}
										}
									}
								}
							}
						}
					}
					:deep(.el-table__body-wrapper) {
						tbody {
							width: 100%;
							tr {
								background: #fff;
								td {
									padding: 10px 0 0;
									color: #666;
									background: #fff;
									border-color: #eee;
									border-width: 0 1px 1px 0;
									border-style: solid;
									text-align: left;
									.cell {
										padding: 0 0 0 5px;
										overflow: hidden;
										word-break: break-all;
										white-space: normal;
										line-height: 24px;
										text-overflow: ellipsis;
										//
										.el-checkbox {
											//
											.el-checkbox__inner {
												background: #fff;
												border-color: #999;
											}
										}
										//
										.is-checked {
											//
											.el-checkbox__inner {
												background: #55ffff;
												border-color: #55ffff;
											}
										}
										// 
										.viewExam_btn {
											border: 0;
											cursor: pointer;
											border-radius: 4px;
											padding: 0 10px;
											margin: 0 5px 10px 0;
											outline: none;
											color: #fff;
											background: #32e1e3;
											width: auto;
											font-size: 14px;
											height: 32px;
										}
										// -
										.viewExam_btn:hover {
											background: #32e1e390;
										}
									}
								}
							}
							tr.el-table__row--striped {
								td {
									background: #FAFAFA !important;
								}
							}
							tr:hover {
								td {
									padding: 10px 0 0;
									color: #333;
									background: #f6f6f6;
									border-color: #eee;
									border-width: 0 1px 1px 0;
									border-style: solid;
									text-align: left;
								}
							}
						}
					}
				}
			}
		}
	}
	// 
	.el-pagination {
		// 
		:deep(.el-pagination__total) {
		}
		// 
		:deep(.btn-prev) {
		}
		// 
		:deep(.btn-next) {
		}
		// 
		:deep(.btn-prev:disabled) {
		}
		// 
		:deep(.btn-next:disabled) {
		}
		// 
		:deep(.el-pager) {
			// 
			.number {
			}
			// 
			.number:hover {
			}
			// 
			.number.is-active {
			}
		}
		// sizes
		:deep(.el-pagination__sizes) {
			.el-select {
				//
				.select-trigger{
					height: 100%;
					.el-input{
						height: 100%;

					}
				}
			}
		}
		// 
		:deep(.el-pagination__jump) {
			// 
			.el-input {

			}
		}
	}
</style>