<template>
	<div>
		<el-dialog custom-class="edit_view" v-model="formVisible" :title="formTitle" width="60%" destroy-on-close>
			<el-form class="add_form" :model="form" label-width="80px" ref="formRef" :rules="rules">
				<el-col :span="24">
					<el-form-item prop="name" label="">
						<el-input class="list_inp" v-model="form.name" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item prop="phone" label="">
						<el-input class="list_inp" v-model="form.phone" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item prop="address" label="">
						<el-input class="list_inp" v-model="form.address" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item prop="isdefault" label="">
						<el-radio-group class="list_radio" v-model="form.isdefault">
							<el-radio label=""></el-radio>
							<el-radio label=""></el-radio>
						</el-radio-group>
					</el-form-item>
				</el-col>
			</el-form>
			<template #footer>
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="formVisible=false"></el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"></el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		ref,
		nextTick,
		getCurrentInstance,
		defineEmits
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const id = ref(0)
	const form = ref({})
	const formRef = ref(null)
	const formVisible = ref(false)
	const formTitle = ref('')
	const resetForm = () => {
		form.value = {
			userid: context?.$toolUtil.storageGet('userid'),
			name: '',
			phone: '',
			isdefault: '',
			address: ''
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	const rules = ({
		name: [{
			required: true,
			message: '',
			trigger: 'blur'
		}],
		phone: [{
			required: true,
			message: '',
			trigger: 'blur'
		}, {
			required: true,
			validator: validateMobile,
			trigger: 'blur'
		}],
		address: [{
			required: true,
			message: '',
			trigger: 'blur'
		}],
	})
	const save = () => {
		formRef.value.validate((valid) => {
			if (valid) {
				context?.$http({
					url: `address/${form.value.id?'update':'add'}`,
					method: 'post',
					data: form.value
				}).then(res => {
					context?.$toolUtil.message(``, 'success', () => {
						formVisible.value = false
						emit('formModelChange')
					})
				})
			}
		})
	}
	//
	const init = (refid=null,text) => {
		resetForm()
		if(refid){
			id.value = refid
			getInfo()
		}
		formTitle.value = text
		formVisible.value = true
	}
	//
	defineExpose({
		init
	})
	//
	const emit = defineEmits(['formModelChange'])
	const getInfo = () => {
		context?.$http({
			url: `address/detail/${id.value}`,
			method: 'get'
		}).then(res => {
			form.value = res.data.data
		})
	}
</script>
<style lang="scss" scoped>

	// 
	.add_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {

				}
				//
				.list_radio {
					//
					.el-radio {
						//
						.el-radio__inner {
						}
						//
						.el-radio__label{
						}
					}
					//
					.is-checked {
						//
						.el-radio__inner {
						}
						//
						.el-radio__label{
						}
					}
				}
			}
		}
	}

	// 
	.formModel_btn_box {
		.formModel_cancel {
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
		}
		.formModel_confirm:hover {
		}
	}
</style>