<template>
	<div class="list-page" :style='{"width":"1200px","padding":"40px 0 0","margin":"20px auto 40px","fontSize":"16px","position":"relative","background":"#fff"}'>
        <div class="breadcrumb-wrapper" style="width: 100%;">
            <div class="back_view">
                <el-button class="back_btn" @click="backClick" type="primary"></el-button>
            </div>
        </div>
		<div class="section_title">
            <span>{{formName}}</span>
		</div>
		<el-tabs v-model="orderStatus" type="card" class="demo-tabs" @tab-change="statusChange" style="width: 100%;">
			<el-tab-pane label="" :name="''"></el-tab-pane>
			<el-tab-pane v-if="btnAuth('orders/','')" label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
		</el-tabs>
		<el-table v-loading="listLoading" border :stripe='true' @selection-change="handleSelectionChange" ref="table"
			:data="list" @row-click="listChange">
			<el-table-column type="selection" width="55" :resizable='true' align="left" header-align="left" />
			<el-table-column label="" width="120" :resizable='true' align="left" header-align="left">
				<template #default="scope">{{ scope.$index + 1}}</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.orderid}}
				</template>
			</el-table-column>
			<el-table-column label="" width="200px" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					<div style="display: flex;align-items: center;">
						<div v-if="scope.row.picture">
							<el-image v-if="scope.row.picture.substring(0,4)=='http'" preview-teleported
								:preview-src-list="[scope.row.picture.split(',')[0]]"
								:src="scope.row.picture.split(',')[0]" style="width:100px;height:100px"></el-image>
							<el-image v-else preview-teleported
								:preview-src-list="[$config.url+scope.row.picture.split(',')[0]]"
								:src="$config.url+scope.row.picture.split(',')[0]" style="width:100px;height:100px">
							</el-image>
						</div>
						<div v-else></div>
						<span style="margin-left: 10px;">{{scope.row.goodname}}</span>
					</div>
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.buynumber}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					<span v-if="scope.row.type!=2" style="font-size: 12px;"></span>{{scope.row.price}} <span
						v-if="scope.row.type==2"></span>
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					<span v-if="scope.row.type!=2" style="font-size: 12px;"></span>{{scope.row.total}} <span
						v-if="scope.row.type==2"></span>
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.address}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.tel}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.consignee}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.remark}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.addtime}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left" width="200px">
				<template #default="scope">
					<el-button class="pay_btn" v-if="scope.row.status==''" type="primary" @click="payClick(scope.row)">
						
					</el-button>
					<el-button class="cancel_btn" v-if="scope.row.status==''" type="danger" @click="cancelClick(scope.row)">
						
					</el-button>
					<el-button class="refundPrice_btn" v-if="scope.row.status==''" type="danger" @click="refundPriceClick(scope.row)">
						
					</el-button>
					<el-button class="refundGood_btn" v-if="scope.row.status==''" type="danger" @click="refundGoodClick(scope.row)">
						
					</el-button>
					<el-button class="logistics_btn" v-if="scope.row.logistics" type="primary" @click="logisticsClick(scope.row)">
						
					</el-button>
					<el-button class="confirm_btn" v-if="scope.row.status==''" type="success" @click="confirmGoodClick(scope.row)">
						
					</el-button>
					<el-button class="toDetail_btn" v-if="scope.row.status==''" type="warning" @click="toDetailClick(scope.row)">
						
					</el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-pagination
			background 
			:layout="layouts.join(',')"
			:total="total" 
			:page-size="listQuery.limit"
            v-model:current-page="listQuery.page"
			prev-text=""
			next-text=""
			:hide-on-single-page="false"
			:style='{}'
			@size-change="sizeChange"
			@current-change="currentChange" />
		<el-dialog v-model="logisticsVisible" title="" width="70%">
			<div v-html="logisticsText"></div>
		</el-dialog>
	</div>
</template>

<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
	} from 'vue';
	import {
		ElMessageBox
	} from 'element-plus'
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const router = useRouter()
	const route = useRoute()
	//
	const tableName = 'orders'
	const formName = ''
	const table = ref(null)
	const selRows = ref([])
	const list = ref([])
	const listLoading = ref(false)
	const listQuery = ref({
		page: 1,
		limit: 20,
		userid: context?.$toolUtil.storageGet('userid'),
		sort:'id',
		order:'desc'
	})
    //
    const orderStatus = ref('')
    if([''  ,'','','','',''].includes(route.query.menuJump)){
        orderStatus.value = route.query.menuJump
    }

    //
    const btnAuth = (e,a)=>{
        return context?.$toolUtil.isBackAuth(e,a)
    }
    //
    const backClick = () => {
        router.push(`/index/${context?.$toolUtil.storageGet('frontSessionTable')}Center`)
    }

	//
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		if (orderStatus.value) {
			params['status'] = orderStatus.value
		}
		context?.$http({
			url: 'orders/list',
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	//
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const total = ref(0)
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//

	//tab
	const statusChange = () => {
		listQuery.value.page = 1
		getList()
	}
	//
	const payClick = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			context?.$http({
				url: `${row.tablename}/info/${row.goodid}`,
				method: 'get'
			}).then(res => {
				let data = res.data.data
				if (userinfo.value.money < row.total) {
					context?.$toolUtil.message('', 'error')
					return
				}
				//
				if (data.jf) {
					userinfo.value.jf = parseInt(userinfo.value.jf) + parseInt(row.total)
				}
				//
				userinfo.value.money = (parseFloat(userinfo.value.money) - parseFloat(row.total)).toFixed(2)
				//
				if (data.alllimittimes) {
					data.alllimittimes = parseInt(data.alllimittimes) - parseInt(row.buynumber)
				}
                //
                context?.$http.post(`${row.tablename}/update`,data)

				//
				context?.$http({
					url: `${context?.$toolUtil.storageGet('frontSessionTable')}/update`,
					method: 'post',
					data: userinfo.value
				}).then(obj => {
					row.status = ''
					//
					context?.$http({
						url: 'orders/update',
						method: 'post',
						data: row
					}).then(res1 => {
                        context.$message.success('')
                        getSession()
                        statusChange()
					})
				})
			})
		}).catch(_ => {})
	}
	//
	const cancelClick = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			//
			row.status = ''
			//
			context?.$http({
				url: 'orders/update',
				method: 'post',
				data: row
			}).then(res1 => {
                context.$message.success('')
                getSession()
                statusChange()
			})
		}).catch(_ => {})
	}
    //,
    const returnLimit = async (order)=>{
        let res = await context.$http.get(`${order.tablename}/info/${order.goodid}`)
        let data = res.data.data
        if(data.alllimittimes){ //
            data.alllimittimes = parseInt(data.alllimittimes) + parseInt(order.buynumber)
            context.$http.post(`${order.tablename}/update`,data)
        }
        return data
    }
	// 
	const refundPriceClick = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(async () => {
            let data = await returnLimit(row)
            row.status = ''
            if (row.type == 2) {
                // 
                userinfo.value.jf = parseInt(userinfo.value.jf) + parseInt(row.total)
            } else {
                // 
                if (data.jf) {
                    userinfo.value.jf = parseInt(userinfo.value.jf) - parseInt(row.total)
                }
                // 
                userinfo.value.money = (parseFloat(userinfo.value.money) + parseFloat(row.total)).toFixed(2)
            }
            // 
            context.$http.post('orders/update',row)
            // 
            context.$http.post(`${context.$toolUtil.storageGet('frontSessionTable')}/update`,userinfo.value).then(res=>{
                context.$message.success("")
                getSession()
                statusChange()
            })
		}).catch(_ => {})
	}
	// 
	const refundGoodClick = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			context?.$http({
				url: `${row.tablename}/info/${row.goodid}`,
				method: 'get'
			}).then(res => {
				let data = res.data.data
				// 
				if (data.alllimittimes) {
					data.alllimittimes = parseInt(data.alllimittimes) + parseInt(row.buynumber)
					// 
					context?.$http({
						url: `${row.tablename}/update`,
						method: 'post',
						data: data
					}).then(obj1 => {})
				}
				if (row.type == 2) {
					// 
					userinfo.value.jf = parseInt(userinfo.value.jf) + parseInt(row.total)
					// 
					context?.$http({
						url: `${context?.$toolUtil.storageGet('frontSessionTable')}/update`,
						method: 'post',
						data: userinfo.value
					}).then(obj => {
						row.status = ''
						// 
						context?.$http({
							url: 'orders/update',
							method: 'post',
							data: row
						}).then(res1 => {
                            context.$message.success('')
                            getSession()
                            statusChange()
						})
					})
				} else {
					// 
					if (data.jf) {
						userinfo.value.jf = parseInt(userinfo.value.jf) - parseInt(row.total)
					}
					// 
					userinfo.value.money = (parseFloat(userinfo.value.money) + parseFloat(row.total)).toFixed(2)
					// 
					context?.$http({
						url: `${context?.$toolUtil.storageGet('frontSessionTable')}/update`,
						method: 'post',
						data: userinfo.value
					}).then(obj => {
						row.status = ''
						// 
						context?.$http({
							url: 'orders/update',
							method: 'post',
							data: row
						}).then(res1 => {
                            context.$message.success('')
                            getSession()
                            statusChange()
						})
					})
				}
			})
		}).catch(_ => {})
	}
	//
	const confirmGoodClick = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			//
			row.status = ''
			//
			context?.$http({
				url: 'orders/update',
				method: 'post',
				data: row
			}).then(res1 => {
                context.$message.success('')
                getSession()
                statusChange()
			})
		}).catch(_ => {})
	}
	//
	const logisticsVisible = ref(false)
	const logisticsText = ref('')
	const logisticsClick = (row) => {
		logisticsText.value = row.logistics
		logisticsVisible.value = true
	}
	//
	const toDetailClick = (row) => {
		router.push(`/index/${row.tablename}Detail?id=${row.goodid}`)
	}
	const userinfo = ref({})
	const getSession = () => {
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			userinfo.value = res.data.data
		})
	}
	const init = () => {
		getSession()
		getList()
	}
	init()
</script>

<style lang="scss" scoped>
    .section_title{
        span{
        }
    }
	// 
	.demo-tabs {
		// 
		:deep(.el-tabs__header) {
			border: 0px solid #e4e7ed;
			padding: 0;
			box-shadow: none;
			margin: 15px 0;
			width: 100%;
			border-width: 1px 0;
			line-height: auto;
			height: auto;
			// 
			.el-tabs__nav-scroll {
				border: 0;
				display: flex;
				width: 100%;
				justify-content: center;
				// list
				.el-tabs__nav {
					border: 0;
					margin: 30px auto;
					width: 100%;
					text-align: center;
					// item
					.el-tabs__item {
						border: 1px solid #eee;
						padding: 0 20px;
						margin: 0 10px 0 0;
						color: #333;
						background: #f6f6f6;
						font-size: 16px;
						line-height: 40px;
						transition: all 0s;
						height: 40px;
					}
					// item active
					.is-active {
						border: 1px solid var(--theme);
						padding: 0 20px;
						margin: 0 10px 0 0;
						color: #fff;
						background: var(--theme);
						font-size: 16px;
						border-color: #fff;
						line-height: 40px;
						height: 40px;
					}
					// item hover
					.el-tabs__item:hover {
						border: 1px solid var(--theme);
						padding: 0 20px;
						color: #fff;
						background: var(--theme);
						border-color: #fff;
						line-height: 40px;
						height: 40px;
					}
				}
			}
		}
	}
	// 
	.el-table {
		padding: 0;
		margin: 20px 0 0;
		background: #fff;
		width: 100%;
		font-size: 15px;
		border-color: var(--theme);
		border-width: 1px 1px 1px 1px;
		border-style: solid;
		:deep(.el-table__header-wrapper) {
			thead {
				color: #333;
				font-weight: 500;
				width: 100%;
				tr {
					background: #fff;
					th {
						padding: 8px 0;
						background: #c3dee3;
						border-color: #eee;
						border-width: 0 0px 0px 0;
						border-style: solid;
						text-align: left;
						.cell {
							padding: 0 10px;
							word-wrap: normal;
							color: var(--theme);
							white-space: nowrap;
							font-weight: bold;
							display: flex;
							vertical-align: middle;
							line-height: 24px;
							text-overflow: ellipsis;
							word-break: break-all;
							width: 100%;
							align-items: center;
							position: relative;
							min-width: 110px;
							//
							.el-checkbox {
								//
								.el-checkbox__inner {
									background: #fff;
									border-color: #999;
								}
							}
							//
							.is-checked {
								//
								.el-checkbox__inner {
									background: var(--theme);
									border-color: var(--theme);
								}
							}
						}
					}
				}
			}
		}
		:deep(.el-table__body-wrapper) {
			tbody {
				width: 100%;
				tr {
					background: #fff;
					td {
						padding: 12px 0;
						color: #666;
						background: #fff;
						border-color: var(--theme);
						border-width: 0 0px 1px 0;
						border-style: solid;
						text-align: left;
						.cell {
							padding: 0 10px;
							overflow: hidden;
							word-break: break-all;
							white-space: normal;
							line-height: 24px;
							text-overflow: ellipsis;
							// 
							.pay_btn {
								border: 0;
								cursor: pointer;
								border-radius: 4px;
								padding: 0 5px;
								margin: 0 5px 10px 0;
								color: #fff;
								background: rgba(85, 200, 85, 1);
								width: auto;
								font-size: 14px;
								height: 32px;
							}
							// -
							.pay_btn:hover {
								background: rgba(85, 200, 85, .8);
							}
							// 
							.cancel_btn {
								border: 0;
								cursor: pointer;
								border-radius: 4px;
								padding: 0 5px;
								margin: 0 5px 10px 0;
								color: #fff;
								background: rgba(200, 75, 75, 1);
								width: auto;
								font-size: 14px;
								height: 32px;
							}
							// -
							.cancel_btn:hover {
								background: rgba(200, 75, 75, .8);
							}
							// 
							.refundPrice_btn {
								border: 0;
								cursor: pointer;
								border-radius: 4px;
								padding: 0 5px;
								margin: 0 5px 10px 0;
								color: #fff;
								background: rgba(240, 160, 140, 1);
								width: auto;
								font-size: 14px;
								height: 32px;
							}
							// -
							.refundPrice_btn:hover {
								background: rgba(240, 160, 140, .8);
							}
							// 
							.refundGood_btn {
								border: 0;
								cursor: pointer;
								border-radius: 4px;
								padding: 0 5px;
								margin: 0 5px 10px 0;
								color: #fff;
								background: rgba(240, 160, 140, 1);
								width: auto;
								font-size: 14px;
								height: 32px;
							}
							// -
							.refundGood_btn:hover {
								background: rgba(240, 160, 140, .8);
							}
							// 
							.logistics_btn {
								border: 0;
								cursor: pointer;
								border-radius: 4px;
								padding: 0 5px;
								margin: 0 5px 10px 0;
								outline: none;
								color: #fff;
								background: rgba(80, 180, 220, 1);
								width: auto;
								font-size: 14px;
								height: 32px;
							}
							// -
							.logistics_btn:hover {
								background: rgba(80, 180, 220, .8);
							}
							// 
							.confirm_btn {
								border: 0;
								cursor: pointer;
								border-radius: 4px;
								padding: 0 5px;
								margin: 0 5px 10px 0;
								color: #fff;
								background: var(--theme);
								width: auto;
								font-size: 14px;
								height: 32px;
							}
							// -
							.confirm_btn:hover {
								background: rgba(65, 128, 230, .8);
							}
							// 
							.toDetail_btn {
								border: 0;
								cursor: pointer;
								border-radius: 4px;
								padding: 0 5px;
								margin: 0 5px 10px 0;
								color: #fff;
								background: rgba(85, 0, 255, 0.5);
								width: auto;
								font-size: 14px;
								height: 32px;
							}
							// -
							.toDetail_btn:hover {
								background: rgba(85, 0, 255, 0.4);
							}
							//
							.el-checkbox {
								//
								.el-checkbox__inner {
									background: #fff;
									border-color: #999;
								}
							}
							//
							.is-checked {
								//
								.el-checkbox__inner {
									background: var(--theme);
									border-color: var(--theme);
								}
							}
						}
					}
				}
				tr.el-table__row--striped {
					td {
						background: #FAFAFA !important;
					}
				}
				tr:hover {
					td {
						padding: 12px 0;
						color: #333;
						background: rgba(245, 245, 245, 1);
						border-color: var(--theme);
						border-width: 0 0px 1px 0;
						border-style: solid;
						text-align: left;
					}
				}
