<template>
	<div class="edit_view" :style='{}'>
        <div class="breadcrumb-wrapper" style="width: 100%;">
            <div class="bread_view">
                <el-breadcrumb separator="/" class="breadcrumb">
                    <el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }"></el-breadcrumb-item>
                    <el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
                </el-breadcrumb>
            </div>
        </div>
        
        <!--  -->
        <div class="appointment-notice">
            <div class="notice-header">
                <i class="el-icon-info-filled"></i>
                <span></span>
            </div>
            <div class="notice-content">
                <p><i class="el-icon-check"></i> </p>
                <p><i class="el-icon-check"></i> </p>
                <p><i class="el-icon-check"></i> 24</p>
                <p><i class="el-icon-check"></i> </p>
            </div>
        </div>
        
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="yuyuebianhao">
						<el-input class="list_inp" v-model="form.yuyuebianhao" placeholder="" readonly></el-input>
						<div class="form-tip"></div>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="zixunshizhanghao">
						<el-select 
							v-model="form.zixunshizhanghao" 
							placeholder="" 
							style="width:100%;"
							filterable
							@change="handleConsultantChange"
							:disabled="!isAdd||disabledForm.zixunshizhanghao?true:false">
							<el-option
								v-for="item in consultantOptions"
								:key="item.value"
								:label="item.label"
								:value="item.value">
							</el-option>
						</el-select>
						<div class="form-tip"></div>
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="lianxifangshi">
						<el-input class="list_inp" v-model="form.lianxifangshi" placeholder=""
							 type="text" readonly />
						<div class="form-tip"></div>
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="yuyueshijian">
						<el-date-picker
							class="list_date"
							v-model="form.yuyueshijian"
							format="YYYY-MM-DD"
							value-format="YYYY-MM-DD"
							type="date"
							:disabled-date="yuyueshijianDisabledDate"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.yuyueshijian?true:false"
							placeholder="" />
						<div class="form-tip"></div>
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="timeSlot">
						<el-select v-model="form.timeSlot" placeholder="" style="width:100%;" :disabled="!form.yuyueshijian || !form.zixunshizhanghao">
							<el-option
								v-for="item in availableTimeSlots"
								:key="item.value"
								:label="item.label"
								:value="item.value">
							</el-option>
						</el-select>
						<div class="form-tip" v-if="availableTimeSlots.length === 0 && form.yuyueshijian && form.zixunshizhanghao">
							
						</div>
						<div class="form-tip" v-else>
							
						</div>
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="duration">
						<el-input-number 
							v-model="form.duration" 
							:min="30" 
							:max="120" 
							:step="30"
							style="width:100%;"
							placeholder="()">
						</el-input-number>
						<div class="form-tip">30-120</div>
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="zhanghao">
						<el-input class="list_inp" v-model="form.zhanghao" placeholder=""
							 type="text" :readonly="!isAdd||disabledForm.zhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="shouji">
						<el-input class="list_inp" v-model="form.shouji" placeholder=""
							 type="text" :readonly="!isAdd||disabledForm.shouji?true:false" />
						<div class="form-tip"></div>
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="yuyuezixunshijian">
						<el-input
							class="list_inp"
							v-model="yuyuezixunshijianDisplay"
							placeholder=""
							readonly>
						</el-input>
						<div class="form-tip"></div>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="yuyuebeizhu">
						<el-input 
							type="textarea" 
							:rows="4" 
							class="textarea_inp" 
							v-model="form.yuyuebeizhu" 
							placeholder=""
							:readonly="!isAdd||disabledForm.yuyuebeizhu?true:false" />
						<div class="form-tip"></div>
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick"></el-button>
				<el-button class="formModel_confirm"
                           @click="save"
                           type="success"
				>
					
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
    import moment from "moment";
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//
	const tableName = 'zixunshiyuyue'
	const formName = ''
	//
	const breadList = ref([{
		name: formName
	}])
	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form
	const form = ref({
		yuyuebianhao: getUUID(),
		zixunshizhanghao: '',
		lianxifangshi: '',
		yuyueshijian: '',
		timeSlot: '',
		duration: 60,
		status: '',
		zhanghao: '',
		shouji: '',
		yuyuezixunshijian: '',
		yuyuebeizhu: '',
		shhf: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		yuyuebianhao : false,
		zixunshizhanghao : false,
		lianxifangshi : false,
		yuyueshijian : false,
		zhanghao : false,
		shouji : false,
		yuyuezixunshijian : false,
		yuyuebeizhu : false,
		sfsh : false,
		shhf : false,
	})
	const isAdd = ref(false)
	//
	const rules = ref({
		yuyuebianhao: [
		],
		zixunshizhanghao: [
			{ required: true, message: '', trigger: 'change' }
		],
		lianxifangshi: [
		],
		yuyueshijian: [
			{ required: true, message: '', trigger: 'change' }
		],
		zhanghao: [
		],
		shouji: [
		],
		yuyuezixunshijian: [
		],
		yuyuebeizhu: [
		],
		sfsh: [
		],
		shhf: [
		],
		timeSlot: [
			{ required: true, message: '', trigger: 'change' }
		],
		duration: [
			{ required: true, message: '', trigger: 'change' }
		]
	})
	// 
	const yuyueshijianDisabledDate = (date) => {
		return date.getTime() < Date.now() - 8.64e7;
	}
	
	// 
	const consultantOptions = ref([])
	// 
	const getConsultantList = () => {
		context?.$http({
			url: `option/zixunshi/zixunshizhanghao`,
			method: 'get'
		}).then(res => {
			if (res.data.code === 0) {
				consultantOptions.value = res.data.data.map(item => ({
					value: item,
					label: item
				}))
			}
		})
	}
	
	// 
	const checkTestScore = () => {
		return new Promise((resolve, reject) => {
			context?.$http({
				url: `ceshipingfen/checkScore`,
				method: 'get'
			}).then(res => {
				if (res.data.code === 0) {
					// 60
					const passScore = res.data.data.pass || false
					if (!passScore) {
						resolve(true) // 
					} else {
						resolve(true) // 
					}
				} else {
					// 
					resolve(true)
				}
			}).catch(() => {
				// 
				resolve(true)
			})
		})
	}
	
	// 
	const handleConsultantChange = (value) => {
		// 
		form.value.timeSlot = ''
		
		// 
		if (value) {
			context?.$http({
				url: `zixunshi/page`,
				method: 'get',
				params: {
					page: 1,
					limit: 1,
					zixunshizhanghao: value
				}
			}).then(res => {
				if (res.data.code === 0 && res.data.data && res.data.data.list && res.data.data.list.length > 0) {
					form.value.lianxifangshi = res.data.data.list[0].lianxifangshi || ''
					console.log(":", form.value.lianxifangshi)
				}
			}).catch(error => {
				console.error(":", error)
			})
		}
		
		// 
		if (form.value.yuyueshijian) {
			getAvailableTimeSlots()
		}
	}

	//methods

	//methods
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
			form.value.yuyueshijian = context?.$toolUtil.getCurDateTime()
			
			// 
			getConsultantList()
			
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='yuyuebianhao'){
					form.value.yuyuebianhao = row[x];
					disabledForm.value.yuyuebianhao = true;
					continue;
				}
				if(x=='zixunshizhanghao'){
					form.value.zixunshizhanghao = row[x];
					disabledForm.value.zixunshizhanghao = true;
					continue;
				}
				if(x=='lianxifangshi'){
					form.value.lianxifangshi = row[x];
					disabledForm.value.lianxifangshi = true;
					continue;
				}
				if(x=='yuyueshijian'){
					form.value.yuyueshijian = row[x];
					disabledForm.value.yuyueshijian = true;
					continue;
				}
				if(x=='zhanghao'){
					form.value.zhanghao = row[x];
					disabledForm.value.zhanghao = true;
					continue;
				}
				if(x=='shouji'){
					form.value.shouji = row[x];
					disabledForm.value.shouji = true;
					continue;
				}
				if(x=='yuyuezixunshijian'){
					form.value.yuyuezixunshijian = row[x];
					disabledForm.value.yuyuezixunshijian = true;
					continue;
				}
				if(x=='yuyuebeizhu'){
					form.value.yuyuebeizhu = row[x];
					disabledForm.value.yuyuebeizhu = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('zhanghao') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.zhanghao = json.zhanghao
				disabledForm.value.zhanghao = true;
			}
			if(json.hasOwnProperty('shouji') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.shouji = json.shouji
				disabledForm.value.shouji = true;
			}
            if (localStorage.getItem('autoSave')) {
                localStorage.removeItem('autoSave')
                save()
            }
		})
	}
	//
	//
	const backClick = () => {
		history.back()
	}
	//
	const save=()=>{
		// 
		if (!form.value.timeSlot) {
			context?.$toolUtil.message('', 'error')
			return
		}
		
		// 
		if (!form.value.yuyueshijian) {
			context?.$toolUtil.message('', 'error')
			return
		}
		
		// 
		checkTestScore().then(canBook => {
			if (!canBook) {
				context?.$toolUtil.message('', 'error')
				return
			}
			
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
									// 
									let successMsg = `${form.value.zixunshizhanghao}${form.value.yuyueshijian} ${form.value.timeSlot}`
									context?.$toolUtil.message(successMsg, 'success', () => {
                                history.back()
									})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
							// 
							let successMsg = `${form.value.zixunshizhanghao}${form.value.yuyueshijian} ${form.value.timeSlot}`
							context?.$toolUtil.message(successMsg, 'success', () => {
                        history.back()
							})
					})
				}
			}
			})
		})
	}
	//
	const changeCrossData=(row)=>{
        if(type.value == 'cross'){
            context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
    onUnmounted(()=>{
        Object.keys(localStorage).map(item=>{
            if(item.startsWith('cross')){
                localStorage.removeItem(item)
            }
        })
    })
	const availableTimeSlots = ref([
		{ value: '09:00-10:00', label: '09:00-10:00' },
		{ value: '10:00-11:00', label: '10:00-11:00' },
		{ value: '11:00-12:00', label: '11:00-12:00' },
		{ value: '14:00-15:00', label: '14:00-15:00' },
		{ value: '15:00-16:00', label: '15:00-16:00' },
		{ value: '16:00-17:00', label: '16:00-17:00' }
	])
	
	// 
	const getAvailableTimeSlots = () => {
		if (!form.value.yuyueshijian || !form.value.zixunshizhanghao) {
			return
		}
		
		context?.$http({
			url: `zixunshiyuyue/getAvailableTimeSlots`,
			method: 'get',
			params: {
				date: form.value.yuyueshijian,
				zixunshizhanghao: form.value.zixunshizhanghao
			}
		}).then(res => {
			if (res.data.code === 0 && res.data.data) {
				// 
				availableTimeSlots.value = res.data.data.map(time => ({
					value: time,
					label: time
				}))
				
				// 
				if (form.value.timeSlot && !res.data.data.includes(form.value.timeSlot)) {
					form.value.timeSlot = ''
				}
			}
		})
	}
	
	// 
	watch(() => form.value.yuyueshijian, (newVal) => {
		if (newVal) {
			getAvailableTimeSlots()
		} else {
			form.value.timeSlot = ''
		}
	})
	
	// 
	watch(() => form.value.zixunshizhanghao, (newVal) => {
		if (newVal && form.value.yuyueshijian) {
			getAvailableTimeSlots()
		}
	})

	// : 
	const yuyuezixunshijianDisplay = computed(() => {
		if (form.value.yuyueshijian && form.value.timeSlot) {
			return `${form.value.yuyueshijian} ${form.value.timeSlot}`
		}
		return ''
	})

	// 
	watch([() => form.value.yuyueshijian, () => form.value.timeSlot], ([date, timeSlot]) => {
		if (date && timeSlot) {
			// 
			const startTime = timeSlot.split('-')[0]
			// 
			form.value.yuyuezixunshijian = `${date} ${startTime}`
		}
	}, { immediate: true })
</script>
