<style>
.edit_view {
    width: 1000px;
    margin: 30px auto;
    padding: 0;
    background: #fff;
    overflow: hidden;
    font-size: 16px;
    color:#666;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
    animation: pageLoad 0.8s ease forwards;
}

@keyframes pageLoad {
    from { opacity: 0; }
    to { opacity: 1; }
}

.edit_view .breadcrumb-wrapper {
    background: linear-gradient(135deg, var(--theme) 0%, #409EFF 100%);
    padding: 20px 30px;
    border-radius: 10px 10px 0 0;
}

.edit_view .bread_view {
    color: #fff;
}

.edit_view .bread_view :deep(.breadcrumb) {
    color: #fff;
}

.edit_view .bread_view :deep(.breadcrumb .el-breadcrumb__separator) {
    color: rgba(255, 255, 255, 0.7);
}

.edit_view .bread_view :deep(.breadcrumb .el-breadcrumb__inner) {
    color: rgba(255, 255, 255, 0.9);
    font-weight: normal;
}

.edit_view .bread_view :deep(.breadcrumb .first_breadcrumb .el-breadcrumb__inner) {
    color: #fff;
    font-size: 18px;
    font-weight: 500;
}

.edit_view .add_form{
    width: 100%;
    padding: 30px 40px 40px;
    border: 0;
    background: #fff;
    animation: fadeInUp 0.6s ease forwards;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.edit_view .add_form .el-form-item{
    margin: 0 0 25px;
    display: flex;
    position: relative;
    opacity: 0;
    animation: fadeIn 0.5s ease forwards;
