<template>
	<div>
		<div class="register_view">
			<el-form :model="registerForm" class="register_form">
				<div class="title_view">{{projectName}}</div>
				<div class="list_item">
					<div class="list_label"></div>
					<input class="list_inp"
					 v-model="registerForm.zhanghao" 
					 placeholder=""
					 type="text"
					 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<input class="list_inp"
					 v-model="registerForm.mima" 
					 placeholder=""
					 type="password"
					 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<input class="list_inp" v-model="registerForm.mima2" type="password" placeholder="" />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<input class="list_inp"
					 v-model="registerForm.xingming" 
					 placeholder=""
					 type="text"
					 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-select 
						class="list_sel" 
						v-model="registerForm.xingbie" 
						placeholder=""
						>
						<el-option v-for="item in yonghuxingbieLists" :label="item" :value="item"></el-option>
					</el-select>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<div class="list_file_list">
						<uploads
							action="file/upload" 
							tip=""
							style="width: 100%;text-align: left;"
							:fileUrls="registerForm.touxiang?registerForm.touxiang:''" 
							@change="touxiangUploadSuccess">
						</uploads>
					</div>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<input class="list_inp"
					 v-model="registerForm.nianling" 
					 placeholder=""
					 type="text"
					 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<input class="list_inp"
					 v-model="registerForm.shouji" 
					 placeholder=""
					 type="text"
					 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<input class="list_inp" v-model="registerForm.email" placeholder="" />
				</div>
				<div class="list_code">
					<div class="list_code_label"></div>
					<div class="list_code_item">
						<input class="list_code_inp" v-model="emailCode" placeholder="" />
						<el-button class="list_code_btn" :disabled="getCodeType" :type="getCodeType?'info':'primary'" @click="getEmailCode">{{setCode}}</el-button>
					</div>
				</div>
				<div class="list_btn">
					<el-button class="register" type="success" @click="handleRegister"></el-button>
					<div class="r-login" @click="close"></div>
				</div>
			</el-form>	
		</div>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
		onMounted,
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context?.$project.projectName
	//
	import { useRoute } from 'vue-router';
	const route = useRoute()
	const tableName = ref('yonghu')

	//
	const getUUID=()=> {
		return new Date().getTime();
	}
	
	const registerForm = ref({
        xingbie: '',
	})
	//
	const time = ref(0)
	const setCode = ref('')
	const getCodeType = ref(false)
	const timer=()=>{
		if(time.value>0){
			getCodeType.value = true
			setCode.value = time.value + 's'
			time.value--
			setTimeout(()=>{
				timer()
			},1000)
		}else{
			time.value = 0
			setCode.value = ''
			getCodeType.value = false
		}
	}
	//
	const emailCode = ref('')
	const getEmailCode=()=>{
		if((!registerForm.value.email)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(!context?.$toolUtil.isEmail(registerForm.value.email)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		time.value = 60
		timer()
		context?.$http({
			url:`${tableName.value}/sendemail?email=`+registerForm.value.email,
			method:'get'
		}).then(res=>{
			context?.$toolUtil.message(``,'success')
		})
	}
	const yonghuxingbieLists = ref([])
	const init=()=>{
		yonghuxingbieLists.value = ",".split(',')
	}
    const touxiangUploadSuccess=(fileUrls)=> {
        registerForm.value.touxiang = fileUrls;
    }
	// 
	//
	const handleRegister = () => {
		let url = tableName.value +"/register";
		if(registerForm.value.mima!=registerForm.value.mima2){
			context?.$toolUtil.message('','error')
			return false
		}
		if(registerForm.value.touxiang!=null){
			registerForm.value.touxiang = registerForm.value.touxiang.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(registerForm.value.nianling&&(!context?.$toolUtil.isIntNumer(registerForm.value.nianling))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.shouji&&(!context?.$toolUtil.isMobile(registerForm.value.shouji))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.email&&(!context?.$toolUtil.isEmail(registerForm.value.email))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.jf&&(!context?.$toolUtil.isNumber(registerForm.value.jf))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.money&&(!context?.$toolUtil.isNumber(registerForm.value.money))){
			context?.$toolUtil.message(``,'error')
			return false
		}
        url=tableName.value +"/register?emailcode="+emailCode.value;
        if(registerForm.value.email&&(!context?.$toolUtil.isEmail(registerForm.value.email))){
            context?.$toolUtil.message('','error')
            return false
        }
		if((!emailCode.value)){
			context?.$toolUtil.message('','error')
			return false
		}
		context?.$http({
			url:url,
			method:'post',
			data:registerForm.value
		}).then(res=>{
			context?.$toolUtil.message('','success', obj=>{
				context?.$router.push({
					path: "/login"
				});
			})
		})
	}
	//
	const close = () => {
		context?.$router.push({
			path: "/login"
		});
	}
	init()
	onMounted(()=>{

	})
</script>
<style lang="scss" scoped>
