		.list_item {
			// label
			.list_label {
			}
			// 
			:deep(.list_inp) {

			}
		}
		// 
		.list_code {
			// label
			.list_code_label {
			}
			// item
			.list_code_item {
				// 
				:deep(.list_code_inp) {

				}
				// 
				.list_code_btn {
				}
			}
		}
		//
		:deep(.list_sel) {
			//
			.select-trigger{
				height: 100%;
				.el-input{
					height: 100%;
				}
			}
		}
		//
		.list_file_list  {
			//
			:deep(.el-upload__tip){
			}
			//
			:deep(.el-upload--picture-card){
				//
				.el-icon{
				}
			}
			:deep(.el-upload-list__item) {
			}
		}
		//
		.list_btn {
			//
			.register {
			}
			//
			.register:hover {
			}
			//
			.r-login {
			}
		}
	}
</style>
