        <div class="breadcrumb-wrapper" style="width: 100%;">
            <div class="bread_view">
                <el-breadcrumb separator="/" class="breadcrumb">
                    <el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }"></el-breadcrumb-item>
                    <el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
                </el-breadcrumb>
            </div>
            <div class="back_view" v-if="centerType">
                <el-button class="back_btn" @click="backClick" type="primary"></el-button>
            </div>
        </div>
		<el-form :inline="true" :model="searchQuery" class="list_search">
			<div class="search_view">
				<div class="search_label">
					
				</div>
				<div class="search_box">
					<el-input class="search_inp" v-model="searchQuery.name" placeholder=""
						clearable>
					</el-input>
				</div>
			</div>
			<div class="search_btn_view">
				<el-button class="search_btn" type="primary" @click="searchClick"></el-button>
			</div>
		</el-form>
		<div class="page_list">
			<div class="data_box">
				<div class="table_view">
					<el-table v-loading="listLoading" border :stripe='true' @selection-change="handleSelectionChange" ref="table"
						:data="list" @row-click="listChange">
						<el-table-column type="selection" width="55" :resizable='true' align="left" header-align="left" />
						<el-table-column label="" width="120" :resizable='true' align="left" header-align="left">
							<template #default="scope">{{ scope.$index + 1}}</template>
						</el-table-column>
						<el-table-column label="" :resizable='true' align="left" header-align="left">
							<template #default="scope">
								<span v-if="scope.row.id === 1" class="personality-test">{{scope.row.name}}</span>
								<span v-else>{{scope.row.name}}</span>
							</template>
						</el-table-column>
						<el-table-column label="()" :resizable='true' align="left" header-align="left">
							<template #default="scope">
								{{scope.row.time}}
							</template>
						</el-table-column>
						<el-table-column label="" :resizable='true' align="left" header-align="left">
							<template #default="scope">
								{{scope.row.status}}
							</template>
						</el-table-column>
						<el-table-column label="" :resizable='true' align="left" header-align="left">
							<template #default="scope">
								<el-button v-if="scope.row.id === 1" class="exam_btn personality-btn" type="primary" @click="examClick(scope.row.id)" size="small"></el-button>
								<el-button v-else class="exam_btn" @click="examClick(scope.row.id)" size="small"></el-button>
							</template>
						</el-table-column>
					</el-table>
				</div>
				<el-pagination
					background 
					:layout="layouts.join(',')"
					:total="total" 
					:page-size="listQuery.limit"
                    v-model:current-page="listQuery.page"
					prev-text=""
					next-text=""
					:hide-on-single-page="false"
					:style='{}'
					@size-change="sizeChange"
					@current-change="currentChange"/>
			</div>
		</div>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		onMounted,
        computed,
	} from 'vue'
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	import {
		ElMessageBox
	} from 'element-plus'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const router = useRouter()
	const route = useRoute()
	
	//
	const tableName = 'exampaper'
	const formName = ''
	const breadList = ref([{
		name: formName
	}])
	//
	onMounted(() => {})
	//
	const list = ref(null)
	const table = ref(null)
	const listQuery = ref({
		page: 1,
		limit: 20,
		sort: 'id',
		status: 1
	})
	const searchQuery = ref({})
	const selRows = ref([])
	const listLoading = ref(false)
	const listChange = (row) => {
		nextTick(() => {
			table.value.clearSelection()
			table.value.toggleRowSelection(row)
		})
	}
	//
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		params['sort'] = 'id'
		params['order'] = 'desc'
		if (searchQuery.value.name && searchQuery.value.name != '') {
			params['name'] = '%' + searchQuery.value.name + '%'
		}
		if(searchQuery.value.name&&searchQuery.value.name!=''){
			params.name = '%' + searchQuery.value.name + '%'
		}
		context?.$http({
			url: `${tableName}/list`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	//
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//
	//
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const total = ref(0)
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//
	//
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	//
	const formRef = ref(null)
	//
	const examClick = (id) => {
		if (id) {
			// ID1
			if (id === 1) {
				// 
				context?.$http({
					url: `examrecord/deleteRecords?userid=${context?.$toolUtil.storageGet('userid')}&&paperid=${id}`,
					method: 'post'
				})
				// 
				router.push(`/examination?id=${id}`)
			} else {
				// 
			context?.$http({
				url: `examrecord/deleteRecords?userid=${context?.$toolUtil.storageGet('userid')}&&paperid=${id}`,
				method: 'post'
			})
			router.push(`/examination?id=${id}`)
			}
		}
	}
	//

	//
	const centerType = ref(false)
	//
	const backClick = () => {
		router.push(`/index/${context?.$toolUtil.storageGet('frontSessionTable')}Center`)
	}
	//
	//
	const init = () => {
		if (route.query.centerType) {
			centerType.value = true
		}
		getList()
	}
	init()
</script>
