<template>
	<div>
		<el-dialog v-model="approvalVisible" :title="''" width="70%" destroy-on-close>
			<el-form ref="ruleFormRef" :model="approvalForm" label-width="120px" :rules="rules">
				<el-row>
					<el-col :span="24">
						<el-form-item label="" prop="shhf">
							<el-input v-model="approvalForm.shhf" type="textarea" />
						</el-form-item>
					</el-col>

				</el-row>
			</el-form>
			<template #footer>
				<span class="dialog-footer" v-if="sfshType==1">
					<el-button type="danger" @click="approvalSave('')"></el-button>
					<el-button type="primary" @click="approvalSave('')">
						
					</el-button>
				</span>
				<span class="dialog-footer" v-if="sfshType==2">
					<el-button type="danger" @click="approvalVisible=false"></el-button>
					<el-button type="primary" @click="approvalSave()">
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>

<script setup>
	import {
		ref,
		toRefs,
		getCurrentInstance,
		defineEmits
	} from 'vue';
	const emit = defineEmits(['shChange'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	//props
	const props = defineProps({
		tableName: String
	})
	const {
		tableName
	} = toRefs(props)
	//props

	const rules = ref({
		shhf: [{
			required: true,
			message: '',
			trigger: 'blur'
		}, ],
	})
	const approvalForm = ref({})
	const approvalVisible = ref(false)
	//ref
	const ruleFormRef = ref(null)
	const sfshType = ref(1)

	const approvalClick = (row,type=1) => {
		console.log('approval:', row)
		sfshType.value = type
		approvalForm.value = JSON.parse(JSON.stringify(row))
		console.log('approval:', approvalForm.value)
		approvalVisible.value = true
	}

	//
	defineExpose({
		approvalClick
	})
	const approvalSave = (type) => {
		console.log('approvalSave, type:', type)
		if(sfshType.value == 1){
			approvalForm.value.sfsh = type
		}
		console.log(', :', approvalForm.value)
		
		// 
		if (type === '') {
			// 
			const id = approvalForm.value.id;
			const apiUrl = `/pdas/zixunshiyuyue/update`;
			
			console.log('fetch:', apiUrl);
			
			// 
			const submitData = {
				id: id,
				sfsh: '',
				shhf: approvalForm.value.shhf || '',
				status: ''
			};
			
			console.log(':', submitData);
			
			// fetch API
			fetch(apiUrl, {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					'token': context?.$toolUtil.storageGet('frontToken')
				},
				body: JSON.stringify(submitData)
			})
			.then(response => {
				console.log(':', response);
				return response.json();
			})
			.then(data => {
				console.log(':', data);
				context?.$toolUtil.message('', 'success', obj => {
					approvalVisible.value = false;
					emit('shChange');
					// 
					setTimeout(() => {
						window.location.reload();
					}, 1000);
				});
			})
			.catch(error => {
				console.error(':', error);
				context?.$toolUtil.message(': ' + error.message, 'error');
			});
			
			return;
		}
		
		ruleFormRef.value.validate((valid) => {
			console.log(':', valid)
			if (valid) {
				// updatesh
				const url = `/pdas/zixunshiyuyue/update`;
				console.log('update:', url);
				
				const submitData = {
					id: approvalForm.value.id,
					sfsh: type || approvalForm.value.sfsh,
					shhf: approvalForm.value.shhf
				};
				
				if (type === '') {
					submitData.status = '';
				} else if (type === '') {
					submitData.status = '';
				}
				
				console.log(':', submitData);
				
				context?.$http({
					url: url,
					method: 'post',
					data: submitData
				}).then(res => {
					console.log(':', res);
					context?.$toolUtil.message('', 'success', obj => {
						approvalVisible.value = false;
						emit('shChange');
						// 
						setTimeout(() => {
							window.location.reload();
						}, 1000);
					});
				}).catch(err => {
					console.error(':', err);
					context?.$toolUtil.message(': ' + (err.message || ''), 'error');
				});
			}
		});
	}
</script>

<style>
</style>
